    longDescription:
    "An E-commerce website that sells footwears. The website used Djangos MVT framework to render dynamic templates. For online payments and refunds, Razorpay is used. The project is hosted on AWS using an EC2 instance and Nginx and Gunicorn is used to ensure security and high availabilty",
    
    description:
    "An E-commerce website that sells footwears. Features like product view, payment, return are implemented.",
    gitHubLink: "https://github.com/augustinejoseph/e-commerce-website",
    liveLink: "http://happyfeetz.in",
    features: [
      "Fully functional e-commerce website",
      "Includes features such as login, cart, wishlist, Razorpay payment, Razorpay refund, and returns",
      "Built using Django and MVT architecture",
      "Utilizes class-based views",
      "Utilizes a production-grade PostgreSQL database",
      "Version control is managed using Git",
      "Code is uploaded to GitHub",
      "Project is hosted on an AWS EC2 instance",
      "Route 53 is used to route traffic to the server",
      "Utilizes a production-grade Nginx web server",
      "Utilizes Gunicorn for server deployment",
      "Integrated online payment and refund using Razorpay",
    ],
    
    
